.main-nav {
  min-width: 320px;
  min-height: 56px;
  margin: 0 auto;
  position: relative;
}
.main-nav__logo {
  display: block;
  height: 56px;
  width: 102px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}
.main-nav__logo-item {
  background-color: #fff;
  order: 1;
  box-shadow: 0 -5px 8px -7px rgba(0, 0, 0, 0.6);
}
.main-nav__toggle {
  position: absolute;
  bottom: 18px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 21px;
  cursor: pointer;
  border: none;
  background-color: #fff;
}
.main-nav .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav__close-cross {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
}
.main-nav__close-cross::before,
.main-nav__close-cross::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 23px;
  height: 2px;
  background-color: #fff;
}
.main-nav__close-cross::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__close-cross::after {
  transform: rotate(-45deg);
}
.main-nav__close-cross:active::before,
.main-nav__close-cross:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
