@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.modal-show {
  display: block;
  animation-name: bounce;
  animation-duration: 0.6s;
}
.modal-outline {
  outline: 4px solid rgba(255, 84, 84, 0.596);
